html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-close {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-main {
  width: 1.23em;
  height: 1em;
  fill: initial;
}

.icon-sights {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-tours {
  width: 1.3em;
  height: 1em;
  fill: initial;
}

.icon-weather {
  width: 1em;
  height: 1em;
  fill: initial;
}

html {
  font-size: 16px;
}

body {
  font-family: 'GothamPro';
  font-weight: 300;
  font-size: 1rem;
  line-height: 1.4;
  line-height-background: #ccc;
}

.container {
  width: 100%;
  max-width: 540px;
  margin: 0 auto;
  padding: 0 20px;
}

@media only screen and (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}

@media only screen and (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}

@media only screen and (min-width: 1200px) {
  .container {
    max-width: 1170px;
  }
}

img {
  max-width: 100%;
  font-size: 0;
  line-height: 0;
  display: block;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

p {
  margin-bottom: 1rem;
}

h1 {
  font-weight: 400;
}

.logo {
  padding: 1rem 0.625rem;
  background: #ff7555;
  color: #ffffff;
  font-weight: 700;
  display: inline-block;
  border-radius: 10px;
  font-size: 1.25rem;
}

.btn {
  min-width: 9rem;
  display: inline-block;
  padding: 0.625rem;
  border: 1px solid;
  border-radius: 25px;
  text-align: center;
  font-weight: 400;
  line-height: 1.2rem;
  transition: all ease 0.2s;
}

.btn--blue {
  background: #405089;
  color: #ffffff;
  border-color: #ffffff;
}

.btn--white {
  background: #ffffff;
  color: #405089;
  border-color: #ffffff;
}

.btn:hover {
  opacity: 0.75;
}

.btn--ml {
  margin-left: 1rem;
}

@font-face {
  font-family: 'GothamPro';
  src: url("../fonts/GothamProRegular/GothamProRegular.woff") format("woff"), url("../fonts/GothamProRegular/GothamProRegular.eot") format("eot"), url("../fonts/GothamProRegular/GothamProRegular.ttf") format("ttf");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'GothamPro';
  src: url("../fonts/GothamProLight/GothamProLight.woff") format("woff"), url("../fonts/GothamProLight/GothamProLight.eot") format("eot"), url("../fonts/GothamProLight/GothamProLight.ttf") format("ttf");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: 'GothamPro';
  src: url("../fonts/GothamProBold/GothamProBold.woff") format("woff"), url("../fonts/GothamProBold/GothamProBold.eot") format("eot"), url("../fonts/GothamProBold/GothamProBold.ttf") format("ttf");
  font-weight: 700;
  font-style: normal;
}

.main {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  background-image: url(../img/topBg.jpg);
  background-position: center center;
  background-size: cover;
}

.main__content {
  max-width: 40rem;
  margin-left: auto;
  padding-top: calc(50vh - 300px);
}

.main__icon {
  fill: #ffffff;
  max-width: 4.25rem;
  max-height: 4.25rem;
  margin-bottom: 0.5rem;
}

.main__title {
  font-size: 3.25rem;
  color: #ffffff;
  font-weight: 700;
  line-height: 3rem;
  margin-bottom: 2rem;
}

@media only screen and (min-width: 992px) {
  .main__title {
    font-size: 4.375rem;
    line-height: 5rem;
  }
}

.main__subtitle {
  font-size: 2rem;
  color: 2rem;
  font-weight: 400;
  color: #ffffff;
  line-height: 1.3;
  margin-bottom: 2rem;
}

.search {
  width: 100%;
}

.search__group {
  position: relative;
}

.search__input {
  width: 100%;
  padding: 1rem 5rem;
  border-radius: 30px;
  font-size: 0.875rem;
  color: #9ea3a8;
  height: 3.5rem;
}

.search__button {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 1.25rem;
  border-radius: 30px;
  padding: 0.875rem 3rem;
  color: #ffffff;
  height: 3.5rem;
  background-image: linear-gradient(left, #5EB6FE, #62d1fd);
}

.search__country {
  position: absolute;
  left: 1.5rem;
  top: 1rem;
}

.navigation {
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.navigation__wrap {
  display: flex;
  align-items: center;
}

.navigation__list {
  display: flex;
  margin-left: 2.5rem;
}

.navigation__item {
  display: block;
  position: relative;
  margin-left: 1.5rem;
}

.navigation__item:after {
  content: ' ';
  position: absolute;
  left: 0;
  bottom: -3px;
  height: 2px;
  background: #ffffff;
  width: 0;
  transition: all ease 0.2s;
}

.navigation__item:hover:after, .navigation__item.active:after {
  width: 1.5rem;
}

.navigation__link {
  color: #ffffff;
  font-weight: 400;
}

.navigation__right {
  margin-left: auto;
}

.subnavigation {
  padding-top: 5rem;
}

.subnavigation__title {
  font-size: 1.375rem;
  font-weight: 700;
  color: #404040;
  margin-bottom: 2rem;
  text-align: center;
}

.subnavigation__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 0 2rem;
  border-radius: 30px;
  margin-bottom: 2rem;
}

.subnavigation__list--shadow {
  box-shadow: 0 0 10px 3px rgba(0, 0, 0, 0.04);
}

.subnavigation__list--center {
  justify-content: center;
}

.subnavigation__item {
  border-radius: 25px;
  margin-left: 1rem;
  margin-right: 1rem;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  transition: all ease 0.3s;
}

.subnavigation__item.active, .subnavigation__item:hover {
  background: #ff7555;
  color: #ffffff;
  fill: #ffffff;
  border-radius: inherit;
}

.subnavigation__item--grey {
  border: 1px solid #eeeeee;
}

.subnavigation__item--grey.active, .subnavigation__item--grey:hover {
  background: #f7f7f8;
}

.subnavigation__link {
  padding: 0.375rem 1rem;
  font-weight: 400;
  display: flex;
  align-items: center;
  color: #c7c6c6;
  transition: all ease 0.3s;
}

.subnavigation__link--grey {
  padding: 0.375rem 1.5rem;
}

.subnavigation__link span {
  margin-left: 0.5rem;
  color: #dadbdd;
}

.subnavigation__icon {
  width: 1.75rem;
  height: 1.75rem;
  margin-right: 1rem;
  border-radius: 0 !important;
  fill: #c7c6c6;
  transition: all ease 0.3s;
}

.subnavigation__icon--close {
  fill: #cccccc;
  width: 0rem;
  height: 1rem;
}

.subnavigation__item.active .subnavigation__link, .subnavigation__item:hover .subnavigation__link,
.subnavigation__item.active .subnavigation__icon, .subnavigation__item:hover .subnavigation__icon {
  fill: #ffffff;
  color: #ffffff;
}

.subnavigation__item--grey.active .subnavigation__link, .subnavigation__item--grey:hover .subnavigation__link,
.subnavigation__item--grey.active .subnavigation__icon, .subnavigation__item--grey:hover .subnavigation__icon {
  color: #dadbdd;
}

.subnavigation__item--grey.active .subnavigation__link span, .subnavigation__item--grey:hover .subnavigation__link span,
.subnavigation__item--grey.active .subnavigation__icon span, .subnavigation__item--grey:hover .subnavigation__icon span {
  color: #c7c6c6;
}

.subnavigation__item--grey.active .subnavigation__icon {
  width: 1rem;
}
